@import url('https://fonts.googleapis.com/css2?family=Black+Han+Sans&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
}

.header {
  width: 100%;
  display: flex;
  position: relative;
}

.main_nav {
  width: calc(100% - 150px);
  position: absolute;
  margin: 30px 100px 0 100px;
  color: whitesmoke;
}

.logo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: initial;
}

.logo_name {
  display: flex;
  font-size: 50px;
  font-family: "Black Han Sans", sans-serif;
  color: black;
}

.nav {
  display: flex;
  width: calc(300px - 10px);
  justify-content: space-between;
  align-items: center;
}

.nav p {
  color: black;
  font-weight: 600;
}

.login_menu {
  display: flex;
  width: 250px;
  justify-content: space-between;
  align-items: center;
}

.login_menu p {
  color: black;
  margin-left: 20px;
  font-weight: 600;
}

.img_container {
  width: 100%;
}

.img_container img {
  width: 100%;
  height: 280px;
  object-fit: cover;
}

.ment {
  width: 400px;
  position: absolute;
  margin: 120px 100px 0 100px;
  color: rgb(255, 255, 255);
  font-weight: bold;
  font-size: 40px;
}

.app_container {
  display: grid;
  place-items: center;
  grid-template-columns: repeat(4, 1fr);
  margin: 20px 200px 0px 200px;
}

.AnimalContainer {
  display: inline-block;
  width: 300px;
  height: 500px;
  border: 1px solid #ccc;
  border-radius: 10px;
  margin: 10px;
  box-shadow: 7px 7px 7px -3px #9b9b9b;
}

.AnimalContainer img {
  width: 100%;
  height: 300px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.AnimalContainer:hover {
  transform: scale(1.05);
}

figure {
  margin: 0;
  padding: 0;
}

.animal_img {
  width: 100%;
  height: auto;
  display: block;
}

.CardBody {
  width: 100%;
  height: 200px;
  padding-top: 15px;
}

.CardBody>ul {
  list-style-type: none;
  text-align: left;
  padding: 0 0 0 15px;
}

.CardBody>ul>li {
  padding-bottom: 5px;
}

.CardName {
  display: flex;
  font-size: 1.5em;
  text-align: left;
  font-weight: 600;
}

.DetailText {
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 250px;
}

.DetailText2 {
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 250px;
  height: auto;
  font-size: 15px;
}

.ageWeight {
  display: flex;
  justify-content: center;
}

.ageWeightInner {
  display: flex;
  width: 270px;
  border-top: solid 1px black;
  padding-top: 5px;
}

.age {
  width: auto;
  border-radius: 5px;
  margin-right: 5px;
  text-align: center;
  background-color: blue;
  color: white;
}

.weight {
  width: auto;
  border-radius: 5px;
  margin-right: 5px;
  text-align: center;
  background-color: blueviolet;
  color: white;
}

.PlaceText {
  display: flex;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 300px;
}

/*
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 250px;
  height: auto;
  font-size: 15px;
*/

.card_actions {
  display: flex;
  justify-content: center;
  margin-top: 15px;
}

/* 페이지 버튼 */

.pagination {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.pagination button {
  display: flex;
  margin-right: 10px;
  width: 40px;
  height: 40px;
  align-items: center;
  justify-content: center;
  border: none;
  background-color: #f0f0f0;
  cursor: pointer;
  border-radius: 100%;
}

.pagination button.active {
  background-color: #007bff;
  color: white;
}

.pagination button:hover {
  background-color: #ccc;
  color: black;
}

.see_more {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50px;
}

.see_more p {
  width: 1300px;
  text-align: center;
  background-color: #007bff;
  color: white;
  border-radius: 5px;
  cursor: pointer;
}

.see_more p:hover {
  background-color: blue;
  color: white;
}