.hsp {
  list-style-type: none;
}

.Rgst_container {
  display: grid;
  place-items: center;
  grid-template-columns: repeat(4, 250px);
  margin: 20px 200px 0 200px;
  justify-content: center;
}

.RgstItem {
  display: inline-block;
  text-align: center;
  width: 200px;
  height: 100px;
  border: 1px solid #ccc;
  border-radius: 30px;
  margin-bottom: 20px;
  box-shadow: 5px 5px 5px -5px #9b9b9b;
  background-color: #f5f5f5;
  color: #666;
}

.RgstContainer:hover {
  transform: scale(1.05);
}

.hsp {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.add {
  width: 100%;
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* 페이지 버튼 */

.pagination {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.pagination button {
  display: flex;
  margin-right: 10px;
  width: 40px;
  height: 40px;
  align-items: center;
  justify-content: center;
  border: none;
  background-color: #f0f0f0;
  cursor: pointer;
  border-radius: 100%;
}

.pagination button.active {
  background-color: #007bff;
  color: white;
}