.animalContainer {
  padding: 20px;
  background-color: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  max-width: 800px;
  margin: 0 auto;
  margin-top: 50px;
}

.title {
  font-size: 30px;
  font-weight: bold;
  margin-top: 20px;
  margin-bottom: 15px;
  text-align: center;
}

.infoContainer {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.infoLeft, .infoRight {
  width: 48%;
}

.infoList {
  list-style: none;
  padding: 0;
  margin: 0;
}

.infoList li {
  display: flex;
  margin-bottom: 10px;
  font-size: 20px;
}

.flex {
  display: flex;
  align-items: center;
}

.specialMark {
  background-color: #e0f7fa;
  padding: 15px;
  border-radius: 8px;
  color: #007bff;
  font-weight: bold;
  text-align: center;
}
