.rgstContainer {
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  margin: 0 auto;
  margin-top: 50px;
}

.infoSection {
  margin-bottom: 20px;
}

.sectionTitle {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
  text-align: center;
}

.infoContainer {
  display: flex;
  justify-content: space-between;
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 8px;
}

.infoColumn {
  width: 100%;
}

.infoList {
  list-style: none;
  padding: 0;
  margin: 0;
}

.infoList li {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  font-size: 16px;
}

.icon {
  margin-right: 10px;
  color: #555;
}

.infoList strong {
  margin-right: 5px;
}
